:root {
  /* primary */
  --dark-cyan-400: #3c8067;
  --cream-400: #f2ebe3;

  /* neutral */
  --dark-blue-700: #1c232b;
  --dark-grayish-blue-700: #6c7289;
  --white: #fff;

  /* fonts */
  --ft-wt-500: 500;
  --ft-wt-700: 700;
  --ft-primary: "Montserrat", sans-serif;
  --ft-secondary: "Fraunces", serif;

  --ft-se-400: 0.875rem;
}
